@font-face {
  font-family: "TAEBAEKmilkyway";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/TAEBAEKmilkyway.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "S-CoreDream-3Light";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
* :focus,
* :focus-visible,
* :target {
  border: none;
  outline: none;
}

body {
  background-color: #434343;
}

.App {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}
.App h1 {
  width: 100%;
  display: block;
  padding: 1rem 0rem;
  text-align: center;
  background: #141414;
  color: #ffffff;
  font-weight: bold;
  font-family: "TAEBAEKmilkyway";
  margin: 0 auto;
}
.App .bigTit {
  width: 100%;
  margin: 0 auto;
  height: auto;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  background: #cecece;
  margin: 0px auto;
  padding: 20px 0;
}
.App .logoArea {
  width: 30%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.App .logoArea .logo {
  width: 100%;
  min-height: 150px;
  height: 100%;
  background: url(../../public/logo.png) 50% 50% no-repeat;
  background-size: contain;
}
.App .logoArea button {
  width: 80%;
  margin: 1rem auto 0;
}
.App .logoArea button {
  padding: 10px 0;
  display: inline-block;
  text-align: center;
  font-family: "S-CoreDream-3Light";
  border: 1px solid rgb(204, 204, 204);
  background: #222;
  color: #fff;
  border-radius: 10px;
  margin: 5px 0;
}
.App .textArea {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 55%;
  height: auto;
}
.App .textArea input {
  width: 96%;
  margin-bottom: 10px;
  border-radius: 5px;
  padding: 0.5rem;
  border: none;
}
.App .textArea input.textTitleArea {
  height: 30px;
  padding: 0.25rem;
}
.App .textArea input.textContentsArea {
  height: 150px !important;
  padding: 0.5rem;
}

.blogArea {
  width: 80%;
  background: #f3f3f3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: auto;
  /**header - nav*/
  /**contents*/
}
.blogArea .header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: auto;
  padding: 12px 10px;
  background: #222;
  border-bottom: 1px solid #333;
  padding-top: 1rem;
}
.blogArea .header h3 {
  display: inline-flex;
  font-size: 1.25rem;
  color: white;
  font-family: "TAEBAEKmilkyway";
  padding-left: 30px;
}
.blogArea .header .nav {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.blogArea .header .nav li {
  display: inline-block;
}
.blogArea .header .nav li.navLi span {
  font-family: "S-CoreDream-3Light";
  color: #fff;
  font-size: 0.65rem;
  display: inline-block;
  padding: 4px;
  border-radius: 2px;
  margin-right: 10px;
  background: #141414;
  letter-spacing: 0.05rem;
}
.blogArea .header .nav li .del {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: #ccc;
  text-align: center;
}
.blogArea .blogContents {
  width: 98%;
  height: 25vh;
  margin: 1% auto;
  background: #ffffff;
  word-wrap: break-word;
  word-break: keep-all;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
.blogArea .blogContents .imgArea {
  width: 35%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}
.blogArea .blogContents .imgArea .pic {
  width: 100%;
  height: 100%;
  padding: 1.5rem 1rem;
  border: 1px solid rgb(204, 204, 204);
}
.blogArea .blogContents .imgArea span > svg {
  width: 40px;
  height: 40px;
}
.blogArea .blogContents .textArea {
  padding: 2rem 3rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  word-wrap: break-word;
  word-break: keep-all;
  font-family: "S-CoreDream-3Light";
  font-size: 0.85rem;
  line-height: 1.5;
  border: 1px solid #ccc;
}
.blogArea .blogContents button {
  width: 40%;
}
.blogArea .blogContents button {
  padding: 10px 0;
  display: inline-block;
  text-align: center;
  font-family: "S-CoreDream-3Light";
  border: 1px solid rgb(204, 204, 204);
  background: #222;
  color: #fff;
  border-radius: 10px;
  margin: 5px 0;
}/*# sourceMappingURL=App.css.map */